.container {
  background: #222;
  padding: 0;
  margin: 0;
  color: #999;
  font-size: 20px;
  min-height: 1000px;
}
a {
  text-decoration: none;
  color: #999;
}
ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
input {
  width: 100%;
  color: #fff;
  font-size: 40px;
  background: #222;
  border: none;
  outline: none;
}
.main_form input {
  border-bottom: 1px dotted #666;
}
.header {
  display: flex;
  width: 800px;
  height: 150px;
  margin: 0 auto;
  justify-content: space-between;
}
button {
  cursor: pointer;
  outline: none;
}
.radis {
  border-radius: 17px;
  background: #262626;
  border: none;
  color: #555;
}
.radis:hover {
  color: #d4d4d4;
}
.circle {
  margin-top: 3px;
  margin-right: 3px;
}
.header .item {
  display: flex;
  padding: 15px 0;
}
.header .item .githubIcon {
  float: right;
}
.content {
  display: flex;
  width: 800px;
  margin: 0 auto;
  flex-wrap: wrap;
  padding: 10px;
}
.content .item {
  margin-top: 20px;
}
.content .sub_content {
  width: 100%;
}
.view_form {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px dotted #666;
}
.view_form .sub_viewForm {
  display: flex;
}
.view_form .sub_viewForm input {
  font-size: 25px;
  border-bottom: none;
}
.view_form .view_input {
  flex-basis: 800px;
}
.sub_content ul li {
  float: left;
  margin: 20px 5px;
}
.padt50 {
  padding-top: 50px;
}
.padb50 {
  padding-bottom: 50px;
}
.task_done {
  text-decoration: line-through #85bf6b;
}
.input_done {
  color: #555;
}
.input_edit input {
  margin-left: 35px;
}
/* login */
.login {
  display: flex;
  margin: 0 auto;
  flex-wrap: wrap;
  width: 800px;
  font-size: 30px;
}
.login .item {
  display: flex;
  width: 100%;
  margin: 0 auto;
  justify-content: center;
  margin: 1px;
}
.login .item button {
  width: 35%;
  padding: 16px 0px;
  color: #999;
  background: #222;
  margin: 5px;
  border: 2px solid #999;
}
.login .item button:hover {
  border: 2px solid #d4d4d4;
}
.cursor {
  cursor: pointer;
}
.button_default {
  padding: 10px;
  background: #e7e7e7;
  color: black;
  margin: 5px;
  border: none;
  border-radius: 3px;
}
.changeColor {
  cursor: pointer;
  margin: 0 auto;
  margin-top: 10px;
}
